<template>
  <BaseLayout title="确认订单">
    <div class="main">

      <div v-if="!state.digiCard">
        <van-cell title="生成提货卡" :border="false">
          <template #right-icon>&nbsp;<van-switch v-model="state.pickCard" size="24" /></template>
        </van-cell>
        <van-cell v-show="state.pickCard" title="提货卡数量">
          <template #right-icon><van-stepper v-model="state.number" theme="round" button-size="22" :max="9999" /></template>
        </van-cell>
        <div v-show="!state.pickCard">
          <van-cell class="card" v-if="state.addr && state.addr.name" size="large" icon="location" :title="state.addr.name + ' ' + state.addr.mobile" :label="state.addr.province + state.addr.city + state.addr.county + state.addr.address" :to="'/my/address/choose?target=' + target" is-link center></van-cell>
          <van-cell class="card" v-else size="large" title="新增收货地址" :to="'/my/address/add?target=' + target" is-link></van-cell>
        </div>
      </div>

      <van-card class="item" v-for="(item, index) in state.list" :key="index" :title="item.title" :desc="item.spec" :thumb="item.image" :price="item.price" :num="item.num" />
      
      <van-cell-group class="fee">
        <van-cell title="商品总金额" :value="total + ' 元'"></van-cell>
        <van-cell title="使用优惠券" value="无" is-link></van-cell>
        <van-cell title="使用余额支付" :value="'（' + profile.cash + ' 可用）'">
          <template #right-icon>&nbsp;<van-switch v-model="state.useCash" size="24" /></template>
        </van-cell>
        <van-cell title="使用积分支付" :value="'（' + profile.point + ' 可用）'">
          <template #right-icon>&nbsp;<van-switch v-model="state.usePoint" size="24" /></template>
        </van-cell>
      </van-cell-group>

      <van-submit-bar label="待支付总金额：" button-text="结算" :style="{bottom:'50px'}" :price="amount*100" :disabled="total == 0 || amount>0" @submit="buy">
        <template #top>
          <div class="notify" v-show="total-profile.cash-profile.point > 0" @click="deposit">账户资产不足，请充值后再来结算 &gt;&gt;&gt;</div>
        </template>
      </van-submit-bar>

    </div>
  </BaseLayout>
</template>

<style scoped>
.main {
  position: relative;
  margin: 10px;
  min-height: 500px;
  padding-bottom: 60px;
}
.card, .item, .fee {
  margin-bottom: 10px;
  background: #FFF;
  border-radius: 5px;
  overflow: hidden;
}
.card::before {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: repeating-linear-gradient(-45deg,#ff6c6c 0,#ff6c6c 20%,transparent 0,transparent 25%,#1989fa 0,#1989fa 45%,transparent 0,transparent 50%);
  background-size: 80px;
  content: '';
}

.notify {
  padding: 8px;
  background: #FFC;
  color: #F96;
  font-size: 12px;
  text-align: center;
}
.red {
  color: red;
}
</style>

<script>
import {reactive, ref} from 'vue'
import {mapState, mapGetters, mapMutations} from 'vuex'
import BaseLayout from '@/components/BaseLayout.vue'

export default {
  components: {BaseLayout},

  props: {
    type: {
      type: String,
      default: "mall",
    },
    direct: {
      type: [String, Number],
      default: 0,
    },
    digital: {
      type: [String, Number],
      default: 0,
    },
    aid: {
      type: [String, Number],
      default: 0,
    },
  },

  data() {
    const state = reactive({
      addr: {},
      list: [],
      number: 1, // 提货卡份数
      amount: 0, // 单份商品金额
      pickCard: false,
      digiCard: false,
      useCash: false,
      usePoint: false,
    });
    return {state};
  },

  created() {
    this.state.digiCard = this.digital > 0;
    
    this.$http.get("/mall/addresslist").then((data) => {
      for (let i in data){
        if ((this.aid == 0 && data[i].status == 2) || (data[i].id == this.aid)){
          this.state.addr = data[i];
          break;
        }
      }
      if (_.isEmpty(this.state.addr) && !_.isEmpty(data)){
        this.state.addr = data[0];
      }
    });

    this.$http.get("/mall/cartlist?status=" + (parseInt(this.direct) + 1)).then((data) => {
      this.state.list = data;
      let amount = 0;
      for (let i in this.state.list){
        amount += this.state.list[i].price * this.state.list[i].num;
      }
      this.state.amount = amount;
    });
  },

  computed: {
    target() {
      return escape(this.$route.fullPath);
    },

    total() {
      return this.state.pickCard ? this.state.amount * this.state.number : this.state.amount;
    },

    amount() {
      let amount = 0;
      if (this.state.usePoint && this.state.useCash)
        amount = this.total - this.profile.point - this.profile.cash
      else if (this.state.usePoint)
        amount = this.total - this.profile.point;
      else if (this.state.useCash)
        amount = this.total - this.profile.cash;
      else
        amount = this.total;
      return _.max([amount, 0]);
    },
    ...mapState(["profile"]),
  },

  methods: {
    deposit() {
      this.$router.push("/my/deposit");
    },

    buy() {
      if (_.isEmpty(this.state.addr) && !this.state.pickCard && !this.state.digiCard){
        return this.$toast("请先选择收货地址");
      }

      let card = 0;
      this.state.pickCard && (card = this.state.number);
      this.state.digiCard && (card = -1);

      let type = "";
      this.state.usePoint && !this.state.useCash && (type = "point");
      !this.state.usePoint && this.state.useCash && (type = "cash");

      this.$http.post("/mall/bill?aid=" + (this.state.pickCard || this.state.digiCard ? 0 : this.state.addr.id) + "&card=" + card + "&direct=" + this.direct).then((data) => {
        this.$http.post("/mall/pay?id=" + data["id"] + "&type=" + type).then(() => {
          this.$router.replace("/my/order");
        });
      });
    }
  }
}
</script>
